  align-items: flex-end;
}

.home-titlePlusPng > img {
  width: 80px;
  margin-left: 10px;
}

.home-second-para {
  font-size: 18px;
  color: grey;
  margin: 20px 0px 20px 0px;
}

.homeContainer-right {
  padding-left: 40px;
  width: 35vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.adopt-btn > * {
  text-decoration: none;
}

@keyframes bounce {
  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
    /* More noticeable scale to enhance the bouncing effect */
  }
}

.Home-button > img {
  margin-left: 7px;

  margin: 0;
  padding: 0;
}

.planning-container {
