  50% {
    transform: scale(1.1);
    /* Increase scale at midpoint for bounce effect */
  }
}

/* Apply animation without conflicting transform */
.navbar-links li > *:hover {
  border: 2px var(--hoverorange) solid;
  background-color: var(--hoverorange);
  color: var(--white);
  box-shadow: 1.5px 1.5px 3px var(--hoverorange);
  cursor: pointer;
  animation: bounce 0.5s ease;
}

.Navbar-button {
  border: 2px solid var(--orange); /* Border with defined color */
  height: 45px; /* Slightly increased height for better clickability */
  width: 120px; /* Adjusted width for balance */
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
  font-size: 16px; /* Slightly larger text for better readability */
  border-radius: 25px; /* More rounded corners for modern look */
  background-color: var(--orange); /* Primary color */
  color: var(--white); /* White text for contrast */
  transition: all 0.3s ease-in-out; /* Smooth transition for hover effects */
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.Navbar-button:hover {
  border: 2px solid var(--hoverorange); /* Border color on hover */
  background-color: var(--hoverorange); /* Background color on hover */
  color: var(--white); /* Keep white text on hover */
  transform: scale(1.1); /* Slightly enlarge the button on hover */
  box-shadow: 2px 2px 10px rgba(255, 100, 50, 0.7); /* Enhanced shadow on hover */
}

@media (max-width: 768px) {
  .navbar-container {
    flex-direction: column;
    align-items: center;
  }

  .navbar-container > * {
    margin-bottom: 10px;
  }
}

