  background: linear-gradient(
    90deg,
    rgba(15, 245, 200, 1) 0%,
    rgba(0, 148, 251, 1) 40%,
    rgba(49, 124, 231, 1) 63%,
    rgba(20, 84, 200, 1) 93%
  );
  /* Applying gradient background */
  font-family: "Roboto", sans-serif;
}

.login-container {
  max-width: 300px;
  padding: 20px;
  padding-left: 60px;
  padding-right: 60px;
  background-color: #fff;
  /* Retains white background for the form */
  border-radius: 8px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
