/* Popup overlay styling with fade-in effect */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: popupFadeIn 0.4s ease-in-out;
}

@keyframes popupFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Popup content box with slide-in effect */
.popup-content {
  background-color: #fff;
  padding: 25px 30px;
  max-width: 500px;
  width: 90%;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
  text-align: center;
  position: relative;
  animation: popupSlideIn 0.4s ease;
}

@keyframes popupSlideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Close button styling with refined look */
.close-btn {
  background-color: #e53935;
  border: none;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 0.9em;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-btn:hover {
  background-color: #c62828;
  transform: scale(1.05);
}

.close-btn:active {
  transform: scale(0.95);
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .popup-content {
    padding: 20px;
    width: 90%;
  }

  .close-btn {
    padding: 8px 15px;
    font-size: 0.85em;
  }
}

